var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"text-center"},[_vm._m(0),_vm._m(1),_vm._v(" "),_vm._m(2),_vm._v(" "),_c('br'),_c('div',{staticClass:"container abstract text-justify"},[_vm._v("Research in many fields has become increasingly reliant on large and complex datasets. Big Data holds untold promise to rapidly advance science by tackling new questions that cannot  be answered with smaller  datasets. While powerful, research with Big Data poses unique challenges, as many standard lab protocols rely on experts examining each one of the samples. This is not feasible for large-scale datasets because manual approaches are time-consuming and hence difficult to scale. Meanwhile, automated approaches lack the accuracy of examination by highly  trained scientists and this may introduce major errors, sources of noise, and unforeseen biases into these large and complex datasets. Our proposed solution is to 1) start with a small, expertly labelled dataset, 2) amplify labels through web-based tools that engage citizen scientists, and 3) train machine learning on amplified labels to emulate expert decision making. As a proof of concept, we developed a system to quality control a large dataset of three-dimensional magnetic resonance images (MRI) of human brains. An initial dataset of 200 brain images labeled by experts were amplified by citizen scientists  to label 722 brains, with over 80,000 ratings done through a simple web interface. A deep learning algorithm was then trained to predict data quality, based on a combination of the citizen scientist labels that accounts for differences in the quality of  classification by different citizen scientists. In an ROC analysis (on left out test data), the deep learning network performed as well as a state-of-the-art, specialized algorithm (MRIQC) for quality control of T1-weighted images, each with an area under the curve of 0.99.Finally, as a specific practicalapplication of the method, we explore how brain image quality relates to the replicability of a well established relationship between brain volume and age over development. Combining citizen science and deep learning can generalize and scale expert decision making; this is particularly important in emerging disciplines where specialized, automated tools do not already exist.")]),_c('div',{staticClass:"container content text-justify mt-3 mb-3"}),_c('h1',{staticClass:"ltx_title_section container text-justify",attrs:{"data-label":"712766"}},[_vm._v("Introduction")]),_c('div',{staticClass:"container content text-justify mt-3 mb-3"},[_vm._v("Many research fields ranging from astronomy, to genomics, to neuroscience are entering an era of Big Data. Large and complex datasets promise to address many scientific questions, but they also present a new set of challenges. For example, over the last few years human neurosciencehas evolved into a Big Data field. In the past, individual groups would each collect their own samples of data from a relatively small group of individuals. More recently, large data sets collected from many thousands of individuals are increasingly more common. This transition has been facilitated through assembly of large aggregated datasets, containing measurements from many individuals, and collected through consortium efforts such as the Human Connectome Project"),_vm._m(3),_vm._v(". These efforts, and the large datasets that they are assembling, promise to enhance our understanding of the relationship between brain anatomy, brain activity and cognition. The field is experiencing a paradigm shift"),_c('cite',{staticClass:"ltx_cite raw v1"},[_vm._v(" ("),_c('a',{attrs:{"href":"#Fan_2014"}},[_vm._v("Fan, 2014")]),_vm._v(" "),_c('a',{directives:[{name:"b-tooltip",rawName:"v-b-tooltip.hover",modifiers:{"hover":true}}],attrs:{"href":"http://europepmc.org/backend/ptpmcrender.fcgi?accid=PMC4236847&blobtype=pdf","title":"View Open Access Article"}},[_c('im