ages, an expert can employ citizen scientists to help with this task, and machine learning can identify which citizen scientists provide expert-quality data. As a proof of concept, we apply this approach to brain MRI quality control (QC): a binary classification task where images are labelled pass or fail based on image quality.QC is a paradigmatic example of the problem of scaling expertise, because a large degree of subjectivity still remains in QC. Each researcher has their own standards as to which images pass or fail on inspection, and this variability may have problematic effects on downstream analyses, especially statistical inference. Effect size estimates may depend on the input data to a statistical model. Varying QC criteria will add more uncertainty to these estimates, and might result in replication failures. For example, in"),_c('cite',{staticClass:"ltx_cite raw v1"},[_vm._v(" ("),_c('a',{attrs:{"href":"#ducharme2016trajectories"}},[_vm._v("Ducharme, 2016")]),_vm._v(" "),_c('a',{directives:[{name:"b-tooltip",rawName:"v-b-tooltip.hover",modifiers:{"hover":true}}],attrs:{"href":"http://europepmc.org/backend/ptpmcrender.fcgi?accid=PMC4691414&blobtype=pdf","title":"View Open Access Article"}},[_c('img',{staticClass:"oabutton",attrs:{"src":require("../assets/icon_OAB.png")}})]),_vm._v(")")]),_vm._v(", the authors found that QC had a significant impact on their estimates of the trajectory of cortical thickness during development. They concluded that post-processing QC (in the form of expert visual inspection) is crucial for such studies, especially due to motion artifacts in younger children. While this was feasible in their study of 398 subjects, this would not be possible for larger scale studies like the ABCD study, which aims to collect data on 10,000 subjects longitudinally "),_vm._m(7),_vm._v(". It is therefore essential that we develop systems that can accurately emulate expert decisions, and that these systems are made openly available for the scient