citizen scientists to almost perfectly emulate those obtained from inspection by experts. Using braindr, citizen scientists amplified the initial expert-labelled dataset (200 3D images) to the entire dataset (> 700 3D images, > 3000 2D slices) in a few weeks. Because braindr is a lightweight web application, users could play it at any time and on any device, and this meant we were able to attract many users. On braindr, each slice received on average 20 ratings, and therefore each 3D brain (consisting of 5 slices) received on average100 ratings. In short, by redesigning the way we interact with our data and presenting it in the web browser, we were able to get many more eyes on our data than would have been possible in a single research lab.")]),_c('div',{staticClass:"container content text-justify mt-3 mb-3"}),_c('h2',{staticClass:"ltx_title_subsection container text-justify",attrs:{"data-label":"578355"}},[_vm._v("Scaling expertise through interactions between experts, citizen scientists and machine learning")]),_c('div',{staticClass:"container content text-justify mt-3 mb-3"}),_c('div',{staticClass:"container content text-justify mt-3 mb-3"},[_vm._v("We found that  an interaction between experts, citizen scientists, and machine learning results in scalable decision-makingon brain MRI images. Recent advances in machine learning have vastly improved image  classification"),_c('cite',{staticClass:"ltx_cite raw v1"},[_vm._v(" ("),_c('a',{attrs:{"href":"#krizhevsky2012imagenet"}},[_vm._v("Krizhevsky, 2012")]),_vm._v(" "),_c('a',{directives:[{name:"b-tooltip",rawName:"v-b-tooltip.hover",modifiers:{"hover":true}}],attrs:{"href":"http://dl.acm.org/ft_gateway.cfm?id=3065386&type=pdf","title":"View Open Access Article"}},[_c('img',{staticClass:"oabutton",attrs:{"src":require("../assets/icon_OAB.png")}})]),_vm._v(")")]),_vm._v(", object detection"),_c('cite',{staticClass:"ltx_cite raw v1"},[_vm._v(" ("),_c('a',{attrs:{"href":"#girshick2014rich"}},[_vm._v("Girshick, 2014")])