at there was an overall tendency for all our users to incorrectly pass images. Future iterations of braindr will include a more informative tutorial and random checks with known images throughout the game to make sure our players are well informed and are performing well throughout the task. In this study, we were able to overcome this limitation because we had enough ratings to train the XGBoost algorithm to preferentially weight some users ratings over others.")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('cite',{staticClass:"ltx_cite raw v1"},[_vm._v(" ("),_c('a',{attrs:{"href":"#simpson2014zooniverse"}},[_vm._v("Simpson, 2014")]),_vm._v(")")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('cite',{staticClass:"ltx_cite raw v1"},[_vm._v(" ("),_c('a',{attrs:{"href":"#gorgolewski2017openneuro"}},[_vm._v("Gorgolewski, 2017")]),_vm._v(")")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container content text-justify mt-3 mb-3"},[_vm._v("The gold-standard subset of the data was created by selecting images that were confidently passed or confidently failed (confidence equal or larger than 4) by the 4 expert raters. In order to measure reliability between expert raters, the ratings of the second, third, and fourth expert expert rater were recoded to a scale of -5 to 5 (where -5 is confidently failed, and 5 is confidently passed). An ROC analysis was performed against the binary ratings of the lead author on the commonly rated images, and the area under the curve (AUC) was computed for each pair. An average AUC, weighted by the number of commonly rated images between the pair, was 0.97, showing good agreement between expert raters. The resulting gold-standard dataset consisted of 200 images. Figure"),_c('span',{staticClass:"au-ref raw v1"},[_c('a',{attrs:{"href":"#fig5"}},[_vm._v("5")])]),_vm._v(" shows example axial slices fr